"use client";

import { Bell } from "lucide-react";
import { Button } from "../ui/button";
import { useState } from "react";
import useSignalR from "@/hooks/use-signalR";
import { NotificationStatus } from "@/communication/responses/@types/Notification";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuGroup,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuShortcut,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu";

export function NotificationComp() {
  const [messages, setMessages] = useState<NotificationStatus[]>([]);

  useSignalR("1", (msg) => {
    setMessages(msg);
  });

  console.log(messages);
  const notificationNoRead = messages.filter(
    (noti) => noti.isRead === false
  ).length;
  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="ghost" size="icon" className="relative">
          <Bell className="h-5 w-5" />
          <span className="absolute -top-1 -right-1 flex h-4 w-4 items-center justify-center rounded-full bg-primary text-[10px] text-white">
            {notificationNoRead}
          </span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="w-80" align="end">
        <DropdownMenuGroup>
          <div>
            <span>Notificações</span>
          </div>
        </DropdownMenuGroup>
        <DropdownMenuSeparator />
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
